  font-family: "Titillium Web", sans-serif;
}
.navbar {
  position: relative !important;
  background-color: rgb(255, 255, 255) !important;
  -webkit-align-self: right;
          align-self: right;
}


.navbar-brand {
  font-size: 1.9rem !important;
  color: #6a74aa !important;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.navbar a:hover {
  color: #da1616 !important;
}


.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #020214;
}

@media (max-width: 991px) {
  .navbar-brand {
     font-size: 1.5rem !important;
  }
  
  .menu_active {
    font-weight: bolder;
    border-bottom: none;
    color: black;
  }
}
.btn_about_us{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 10px;
  transition: 0.5s;
  color: #e2212a;
  border: 2px solid #e2212a;
  text-decoration: none;

}
.btn_about_us:hover{
  color:#fff;
  background: #e2212a;
  }
.btn_events{
  font-weight: 1000;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
