*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}
.bottom_background{
    font-size: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
    background-color: rgb(88, 148, 125);
    height: "100%";
    width: "100%";
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: transparent;
    width: 100vw;
    height: 50vh;
    position: fixed;
    left: 0;
    top: 50vh;
    -webkit-transform: none!important;
            transform: none!important;
    transition: transform .5s cubic-bezier(.215,.61,.355,1),opacity .25s ease,-webkit-transform .5s cubic-bezier(.215,.61,.355,1);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    background-color: rgb(26, 156, 156);
}
.contact_align{
    text-transform: uppercase;
    text-align: center;
}



.flashy-img {
    border-radius: 800000000000px;
    border-bottom-left-radius: 8000000px;
    border-top-left-radius: 8000px;
    border-bottom-right-radius: 8000px;
    border-top-right-radius: 8000px;
}

.circ-img {
    border-bottom-left-radius: 50% 50%;
    border-top-left-radius: 50% 50%;
    border-bottom-right-radius: 50% 50%;
    border-top-right-radius: 50% 50%;
}
